.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 99;
  background-color: #00000055;

  > .result-pop-up {
    background: #ffffff;
    color: #000;
    z-index: 100;
    padding: 30px;
    border-radius: 20px;
    min-width: 50vw;
    top: 10vh;
    position: absolute;
    width: 60%;
  }
}
