@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Patrick+Hand&display=swap&subset=latin-ext);
html,body{width:100%;height:100%}body{margin:0;font-family:'Patrick Hand', cursive, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body h1,body h2,body h3,body h4,body h5{font-family:'Permanent Marker', cursive}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.start_root__nvgyY{display:flex;flex-direction:column;justify-content:center;align-self:center;margin:30px}.start_root__nvgyY>h2{text-align:center}.start_input-label__1wIFV>input{width:100%}

.leave-game{display:flex}.leave-game>button{background-color:#ee5253;border:0;color:white;font-size:1rem;padding:10px 20px;border-radius:5px}.leave-game>button:hover{cursor:pointer}

p{color:white;font-size:2rem;text-transform:capitalize;text-align:center}p>span{padding-right:10px;font-size:3rem}

@-webkit-keyframes pulse{0%{transform:scale(1)}50%{transform:scale(1.07)}100%{transform:scale(1)}}@keyframes pulse{0%{transform:scale(1)}50%{transform:scale(1.07)}100%{transform:scale(1)}}.question{display:flex;align-items:center;justify-content:center;background-color:white;border-radius:100%;box-shadow:3px 3px 5px rgba(0,0,0,0.1);position:relative;width:6vw;height:6vw;min-width:50px;min-height:50px;max-width:300px;max-height:300px;margin-top:20px}.question>span{position:absolute;left:-1rem;top:-1rem;font-size:3rem}.focused{-webkit-animation:pulse 1.1s infinite ease-in-out;animation:pulse 1.1s infinite ease-in-out}.disabled{background-color:gray !important}

@-webkit-keyframes zoom-fade-out{0%{font-size:2rem;opacity:1}100%{font-size:30rem;opacity:0}}@keyframes zoom-fade-out{0%{font-size:2rem;opacity:1}100%{font-size:30rem;opacity:0}}.game-screen{display:flex;flex-direction:column;height:100vh;width:100vw;background-color:paleturquoise}.game-screen:-webkit-full-screen{background-color:#fff}.game-screen:-ms-fullscreen{background-color:#fff}.game-screen:fullscreen{background-color:#fff}.game-screen>game-count-down{-webkit-animation:zoom-fade-out 1s ease-in-out;animation:zoom-fade-out 1s ease-in-out;-webkit-animation-iteration-count:4;animation-iteration-count:4;position:absolute;left:50%;transform:translate(-50%, -50%);top:40%;opacity:1}.game-screen header{position:absolute}.game-screen main{display:flex;height:100%}.game-screen main section{width:100%;min-width:calc(100% / 8);display:flex;flex-wrap:wrap;flex-direction:column;align-items:center;justify-content:center}.game-screen main section>.question-wrapper{min-height:calc(30vw + 5*20px);display:flex;flex-direction:column;justify-content:flex-end}

@-webkit-keyframes start-animation{0%{opacity:1;transform:scale(0)}80%{transform:scale(1)}100%{opacity:0}}@keyframes start-animation{0%{opacity:1;transform:scale(0)}80%{transform:scale(1)}100%{opacity:0}}.game-started{position:absolute;width:100vw;height:100vh}.game-started h1{display:flex;margin-top:20%;justify-content:center;align-items:center;font-size:12vw;position:relative;z-index:1;opacity:0}.game-started .start-animation{-webkit-animation:start-animation 1s 1 ease-in-out;animation:start-animation 1s 1 ease-in-out}

.wrapper{width:100%;height:100%;display:flex;justify-content:center;align-items:center;position:absolute;z-index:99;background-color:#00000055}.wrapper>.question-pop-up{color:#ffffff;z-index:100;padding:30px;border-radius:20px;min-width:50vw;top:10vh;position:absolute;width:60%}.wrapper>.question-pop-up>span{position:absolute;font-size:6rem;top:-3rem;left:-3rem}.wrapper>.question-pop-up>section{display:flex;flex-direction:row;justify-content:space-around}

@-webkit-keyframes roll-up{from{transform:translateY(40px)}to{transform:translateY(0px)}}@keyframes roll-up{from{transform:translateY(40px)}to{transform:translateY(0px)}}.score{overflow:hidden;margin:20px}.score>h1{color:white;margin:0}.score>.roll-up{-webkit-animation:roll-up ease-in-out 1s 1;animation:roll-up ease-in-out 1s 1}.score>.move-under{transform:translateY(40px)}

.wrapper{width:100%;height:100%;display:flex;justify-content:center;align-items:center;position:absolute;z-index:99;background-color:#00000055}.wrapper>.result-pop-up{background:#ffffff;color:#000;z-index:100;padding:30px;border-radius:20px;min-width:50vw;top:10vh;position:absolute;width:60%}

.game-controller{display:flex;flex-direction:column;justify-content:space-between;height:100vh}.game-controller header{display:flex;flex-direction:column;align-items:center;padding:10px 0}.game-controller header>h2{color:black;display:flex;justify-content:space-between;margin:10px 30px;width:100%}.game-controller header>h2>span{margin:0 10px}.game-controller header>span{font-size:2rem;width:90%}.game-controller header>span>input{color:white;width:calc(100% - 2rem - 10px);font-size:2rem;border:none;outline:none;text-align:center;text-transform:capitalize;background:none}.game-controller header>span>input[type=text]::-webkit-input-placeholder{color:white}.game-controller header>span>input[type=text]:-ms-input-placeholder{color:white}.game-controller header>span>input[type=text]::-ms-input-placeholder{color:white}.game-controller header>span>input[type=text]::placeholder{color:white}.game-controller main{display:flex;flex-direction:column;align-items:center;padding:20px;height:100%}.game-controller main>h1{text-align:center}.game-controller footer{width:100%}.game-controller footer>button{width:50%;padding:1.6rem 1.5rem 1.4rem;font-size:2.8rem;border-style:inset;outline:none}.game-controller footer>button:only-child{width:100%}.game-controller footer>button:first-child{border:10px solid #ff6b6b}.game-controller footer>button:last-child{border:10px solid #1dd1a1}

h1{text-align:center}

main>.question{position:relative}main>.question .question{min-width:80px;min-height:80px;margin-top:0}main>.question>button{padding:10px;margin:0px 20px}main>.question>span{position:absolute;left:-2rem;top:-2.3rem;font-size:3rem;z-index:1}


main>p{color:black}

main>form{display:flex;width:100%;justify-content:center}main>form>textarea{margin:0px;width:90%;min-height:200px;border:1px solid #cccccc;border-radius:4px;font-size:1.4rem}

